#map-container {
  display: flex;
  justify-content: center;
}

path {
  stroke: black;
  cursor: pointer;
}

path:hover {
  fill: blue;
  opacity: 0.5;
}

.selected {
  stroke: black;
  stroke-width: 4px;
  cursor: pointer;
}

.name {
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

h2 {
  margin: -20px 0 10px 10px;
}
